.wrap {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .button {
    min-width: 300px;
    min-height: 60px;
    font-family: 'Nunito', sans-serif;
    font-size: 22px;
    text-transform: uppercase;
    letter-spacing: 1.3px;
    font-weight: 700;
    color: #313133;
    background: linear-gradient(90deg, rgba(223,158,12,255) 20%, rgba(250,186,17,255) 80%);
    border: none;
    border-radius: 100px;
    box-shadow: 12px 12px 24px rgba(23,114,145,255);
    transition: all 0.3s ease-in-out 0s;
    cursor: pointer;
    outline: none;
    position: relative;
    padding: 10px;
  }
  
  .button::before {
    content: '';
    border-radius: 1000px;
    min-width: calc(300px + 12px);
    min-height: calc(60px + 12px);
    border: 6px solid var(--color-golden);
    box-shadow: 0 0 60px rgba(23,114,145,255);
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    opacity: 0;
    transition: all .0s ease-in-out 0s;
  }
  
  .button:hover {
    color: #313133;
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
    border: 3px solid var(--color-golden);
  }
  
  .button:hover::before {
    opacity: 1;
  }
  
  .button.clicked::after {
    content: '';
    width: 30px; height: 30px;
    border-radius: 100%;
    border: 70px solid var(--color-golden);
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-animation: ring 1s;
            animation: ring 1s;
  }
  
  
  
  @-webkit-keyframes ring {
    
    50% {
      width:  2000px;
      height: 2000px;
      opacity: 0.1;
      z-index: -2;
    }
    
    100% {
      width:  8000px;
      height: 8000px;
      opacity: 1;
      z-index: -2;
    }
  }
  
  
  
  @keyframes ring {
    
    50% {
      width:  2000px;
      height: 2000px;
      opacity: 0.1;
      z-index: -2;
    }
    
    100% {
      width:  8000px;
      height: 8000px;
      opacity: 1;
      z-index: -2;
    }
  }
  
